<template>
  <div id="formArea">
    <table cellpadding="10">
      <tr>
        <th>
          <label for="name">第三方名称 :</label>
        </th>
        <td>
          <input type="text"
                 placeholder="请填写第三方名称"
                 id="name"
                 name="name"
                 autocomplete="off"
                 v-model="third_name"
                 @input="passBasicData"
                 lazy>
        </td>
      </tr>
      <tr>
        <th>支付方式 : </th>
        <td>
          <el-select v-model="payment"
                     name="payment"
                     @change="passBasicData">
            <el-option v-for="item in options"
                       :key="item.value"
                       :label="item.label"
                       :value="item.value"></el-option>
          </el-select>
        </td>
      </tr>
      <tr>
        <th>
          <label for="formUrl">传送地址 :</label>
        </th>
        <td>
          <input type="text"
                 id="formUrl"
                 name="formUrl"
                 autocomplete="off"
                 v-model="form_url"
                 @input="passBasicData">
        </td>
      </tr>
      <tr>
        <th>金额单位 : </th>
        <td>
          <el-select v-model="amount_unit"
                     name="amount_unit"
                     @input="passBasicData">
            <el-option v-for="fee in fees"
                       :key="fee.value"
                       :label="fee.label"
                       :value="fee.value"></el-option>
          </el-select>
        </td>
      </tr>
      <tr>
        <th>订单金额取小数点后</th>
        <td>
          <el-select v-model="decimal"
                     name="decVal"
                     @input="passBasicData">
            <el-option v-for="count in counts"
                       :key="count.value"
                       :label="count.label"
                       :value="count.value"></el-option>
          </el-select>
        </td>
      </tr>
    </table>
  </div>
</template>
<script>
export default {
  name: 'formArea',
  data() {
    return {
      options: [
        { label: '网银直连', value: 'wy' },
        { label: '网银手机端', value: 'wyh5' },
        { label: '银联快捷', value: 'ylkj' },
        { label: '银联快捷手机端', value: 'ylkjh5' },
        { label: '银联钱包扫码', value: 'ylbs' },
        { label: '银联钱包手机端', value: 'ylh5' },
        { label: '银联钱包反扫', value: 'ylfs' },
        { label: '微信扫码', value: 'wxbs' },
        { label: '微信手机端', value: 'wxh5' },
        { label: '微信反扫', value: 'wxfs' },
        { label: '京东钱包扫码', value: 'jdbs' },
        { label: '京东钱包手机端', value: 'jdh5' },
        { label: '京东钱包反扫', value: 'jdfs' },
        { label: '支付宝扫码', value: 'zfbbs' },
        { label: '支付宝手机端', value: 'zfbh5' },
        { label: '支付宝反扫', value: 'zfbfs' },
        { label: 'QQ扫码', value: 'qqbs' },
        { label: 'QQ钱包手机端', value: 'qqh5' },
        { label: 'QQ钱包反扫', value: 'qqfs' },
        { label: '百度扫码', value: 'bdbs' },
        { label: '百度手机端', value: 'bdh5' },
        { label: '百度反扫', value: 'bdfs' },
      ],
      counts: [
        { label: '0位', value: 0 },
        { label: '1位', value: 1 },
        { label: '2位', value: 2 },
      ],
      fees: [{ label: '元', value: 1 }, { label: '分', value: 2 }],
      // 选择
      third_name: '', //第三方支付名稱
      payment: '', //支付渠道
      form_url: '', // 傳送地址
      amount_unit: '', // 單位
      decimal: '', // 小數點
      basicData: {},
    };
  },
  methods: {
    passBasicData() {
      this.basicData = {
        third_name: this.third_name,
        payment: this.payment,
        form_url: this.form_url,
        amount_unit: this.amount_unit,
        decimal: this.decimal,
      };
      this.$bus.$emit('emit2PHPdata1', this.basicData);
    },
  },
};
</script>
<style lang="scss" scoped>
#formArea {
  padding-bottom: 15px;
  padding-left: 20px;
  border-bottom: 1px solid #707c74;
  padding-top: 30px;
}

#formArea table {
  width: 100%;
}
</style>



